"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import { 
  FileImage, 
  HardDrive, 
  TrendingUp, 
  Users,
  Image,
  Video,
  FileText,
  Upload,
  Download,
  FolderOpen
} from "lucide-react";
import { 
  assets,
  projects,
  team
} from "@/lib/utils/dummy-data";

export function AssetsStats() {
  // Calculate comprehensive asset statistics
  const totalAssets = assets.length;
  
  // File type distribution
  const imageAssets = assets.filter(a => a.type === "image");
  const videoAssets = assets.filter(a => a.type === "video");
  const documentAssets = assets.filter(a => a.type === "pdf" || a.type === "document");
  const otherAssets = assets.filter(a => a.type === "other");
  
  // Storage analysis
  const totalSize = assets.reduce((sum, asset) => sum + asset.fileSize, 0);
  const avgFileSize = totalAssets > 0 ? totalSize / totalAssets : 0;
  const largestFile = Math.max(...assets.map(a => a.fileSize));
  
  // Recent activity (last 7 days)
  const last7Days = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000);
  const recentAssets = assets.filter(a => new Date(a.updatedAt) > last7Days);
  
  // Project distribution
  const projectsWithAssets = projects.filter(p => 
    assets.some(a => a.projectId === p.id)
  ).length;
  
  // Team activity
  const teamMembersWithAssets = new Set(assets.map(a => a.addedBy)).size;
  const assignedAssets = assets.filter(a => a.assignedTo).length;
  
  const formatFileSize = (bytes: number) => {
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    if (bytes === 0) return '0 Byte';
    const i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)).toString());
    return Math.round(bytes / Math.pow(1024, i) * 100) / 100 + ' ' + sizes[i];
  };

  // Storage usage percentage (assuming 10GB limit)
  const storageLimit = 10 * 1024 * 1024 * 1024; // 10GB in bytes
  const storageUsed = (totalSize / storageLimit) * 100;

  const stats = [
    {
      title: "Storage Usage",
      value: formatFileSize(totalSize),
      description: `${Math.round(storageUsed)}% of 10GB used`,
      icon: HardDrive,
      color: storageUsed > 80 ? "text-red-600" : storageUsed > 60 ? "text-orange-600" : "text-green-600",
      bgColor: storageUsed > 80 ? "bg-red-50" : storageUsed > 60 ? "bg-orange-50" : "bg-green-50",
      progress: Math.min(storageUsed, 100),
      trend: `${formatFileSize(avgFileSize)} avg file size`
    },
    {
      title: "Total Assets",
      value: totalAssets,
      description: "Files across all projects",
      icon: FileImage,
      color: "text-blue-600",
      bgColor: "bg-blue-50",
      progress: Math.min((totalAssets / 100) * 100, 100),
      trend: `${recentAssets.length} added this week`
    },
    {
      title: "Active Projects",
      value: projectsWithAssets,
      description: "Projects with assets",
      icon: FolderOpen,
      color: "text-purple-600",
      bgColor: "bg-purple-50",
      progress: Math.round((projectsWithAssets / projects.length) * 100),
      trend: `${projects.length - projectsWithAssets} without assets`
    },
    {
      title: "Team Usage",
      value: teamMembersWithAssets,
      description: "Active team members",
      icon: Users,
      color: "text-green-600",
      bgColor: "bg-green-50",
      progress: Math.round((teamMembersWithAssets / team.length) * 100),
      trend: `${assignedAssets} assets assigned`
    }
  ];

  return (
    <div className="space-y-6">
      {/* Main Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {stats.map((stat) => {
          const Icon = stat.icon;
          return (
            <Card key={stat.title} className="relative overflow-hidden">
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium text-muted-foreground">
                  {stat.title}
                </CardTitle>
                <div className={`p-2 rounded-md ${stat.bgColor}`}>
                  <Icon className={`h-4 w-4 ${stat.color}`} />
                </div>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{stat.value}</div>
                <p className="text-xs text-muted-foreground mt-1">
                  {stat.description}
                </p>
                
                {/* Progress Bar */}
                <div className="mt-3">
                  <div className="flex items-center justify-between text-xs mb-1">
                    <span className="text-muted-foreground">Usage</span>
                    <span className="font-medium">{Math.round(stat.progress)}%</span>
                  </div>
                  <Progress value={stat.progress} className="h-2" />
                </div>
                
                <div className="flex items-center mt-3">
                  <Badge variant="secondary" className="text-xs">
                    {stat.trend}
                  </Badge>
                </div>
              </CardContent>
            </Card>
          );
        })}
      </div>

      {/* Detailed Analytics */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* File Type Distribution */}
        <Card>
          <CardHeader>
            <CardTitle className="text-sm font-medium">File Type Distribution</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Image className="h-4 w-4 text-blue-500" />
                <span className="text-sm">Images</span>
              </div>
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium">{imageAssets.length}</span>
                <div className="w-16 bg-muted rounded-full h-2">
                  <div 
                    className="bg-blue-500 h-2 rounded-full" 
                    style={{ width: `${(imageAssets.length / totalAssets) * 100}%` }}
                  />
                </div>
              </div>
            </div>
            
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Video className="h-4 w-4 text-purple-500" />
                <span className="text-sm">Videos</span>
              </div>
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium">{videoAssets.length}</span>
                <div className="w-16 bg-muted rounded-full h-2">
                  <div 
                    className="bg-purple-500 h-2 rounded-full" 
                    style={{ width: `${(videoAssets.length / totalAssets) * 100}%` }}
                  />
                </div>
              </div>
            </div>
            
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <FileText className="h-4 w-4 text-green-500" />
                <span className="text-sm">Documents</span>
              </div>
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium">{documentAssets.length}</span>
                <div className="w-16 bg-muted rounded-full h-2">
                  <div 
                    className="bg-green-500 h-2 rounded-full" 
                    style={{ width: `${(documentAssets.length / totalAssets) * 100}%` }}
                  />
                </div>
              </div>
            </div>
            
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <FolderOpen className="h-4 w-4 text-orange-500" />
                <span className="text-sm">Other</span>
              </div>
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium">{otherAssets.length}</span>
                <div className="w-16 bg-muted rounded-full h-2">
                  <div 
                    className="bg-orange-500 h-2 rounded-full" 
                    style={{ width: `${(otherAssets.length / totalAssets) * 100}%` }}
                  />
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Storage Breakdown */}
        <Card>
          <CardHeader>
            <CardTitle className="text-sm font-medium">Storage Breakdown</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-sm">Images</span>
              <span className="text-sm font-medium">
                {formatFileSize(imageAssets.reduce((sum, a) => sum + a.fileSize, 0))}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm">Videos</span>
              <span className="text-sm font-medium">
                {formatFileSize(videoAssets.reduce((sum, a) => sum + a.fileSize, 0))}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm">Documents</span>
              <span className="text-sm font-medium">
                {formatFileSize(documentAssets.reduce((sum, a) => sum + a.fileSize, 0))}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm">Other Files</span>
              <span className="text-sm font-medium">
                {formatFileSize(otherAssets.reduce((sum, a) => sum + a.fileSize, 0))}
              </span>
            </div>
            <div className="border-t pt-2 mt-2">
              <div className="flex items-center justify-between font-medium">
                <span className="text-sm">Total</span>
                <span className="text-sm">{formatFileSize(totalSize)}</span>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Recent Activity */}
        <Card>
          <CardHeader>
            <CardTitle className="text-sm font-medium">Recent Activity</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Upload className="h-4 w-4 text-green-500" />
                <span className="text-sm">Uploaded</span>
              </div>
              <span className="text-sm font-medium">{recentAssets.length} this week</span>
            </div>
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Download className="h-4 w-4 text-blue-500" />
                <span className="text-sm">Downloaded</span>
              </div>
              <span className="text-sm font-medium">24 times</span>
            </div>
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <TrendingUp className="h-4 w-4 text-purple-500" />
                <span className="text-sm">Most Popular</span>
              </div>
              <span className="text-sm font-medium">Hero Banner</span>
            </div>
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <FileImage className="h-4 w-4 text-orange-500" />
                <span className="text-sm">Largest File</span>
              </div>
              <span className="text-sm font-medium">{formatFileSize(largestFile)}</span>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}