"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Badge } from "@/components/ui/badge";
import { 
  Upload, 
  Filter, 
  Download, 
  MoreHorizontal,
  FileImage,
  Grid3X3,
  List,
  FolderOpen,
  Image,
  Video,
  FileText
} from "lucide-react";
import { assets } from "@/lib/utils/dummy-data";

type ViewMode = "grid" | "list";

interface AssetsHeaderProps {
  viewMode?: ViewMode;
  onViewModeChange?: (mode: ViewMode) => void;
}

export function AssetsHeader({ viewMode = "grid", onViewModeChange }: AssetsHeaderProps) {
  const [currentView, setCurrentView] = useState<ViewMode>(viewMode);

  // Calculate asset stats
  const totalAssets = assets.length;
  const imageAssets = assets.filter(a => a.type === "image").length;
  const videoAssets = assets.filter(a => a.type === "video").length;
  const documentAssets = assets.filter(a => a.type === "pdf" || a.type === "document").length;
  const otherAssets = assets.filter(a => a.type === "other").length;
  
  // Calculate total file size
  const totalSize = assets.reduce((sum, asset) => sum + asset.fileSize, 0);
  const formatFileSize = (bytes: number) => {
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    if (bytes === 0) return '0 Byte';
    const i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)).toString());
    return Math.round(bytes / Math.pow(1024, i) * 100) / 100 + ' ' + sizes[i];
  };

  const handleViewChange = (mode: ViewMode) => {
    setCurrentView(mode);
    onViewModeChange?.(mode);
  };

  return (
    <div className="flex flex-col space-y-4">
      {/* Main Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Assets</h1>
          <p className="text-muted-foreground">
            Manage and organize files across all your projects
          </p>
        </div>
        <div className="flex items-center space-x-2">
          <Button variant="outline" size="sm">
            <Download className="h-4 w-4 mr-2" />
            Export
          </Button>
          <Button size="sm">
            <Upload className="h-4 w-4 mr-2" />
            Upload Assets
          </Button>
        </div>
      </div>

      {/* Stats and Controls */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <Badge variant="outline" className="flex items-center space-x-1">
            <FileImage className="h-3 w-3" />
            <span>{totalAssets} Total Assets</span>
          </Badge>
          <Badge variant="secondary" className="flex items-center space-x-1">
            <Image className="h-3 w-3" />
            <span>{imageAssets} Images</span>
          </Badge>
          <Badge variant="secondary" className="flex items-center space-x-1">
            <Video className="h-3 w-3" />
            <span>{videoAssets} Videos</span>
          </Badge>
          <Badge variant="secondary" className="flex items-center space-x-1">
            <FileText className="h-3 w-3" />
            <span>{documentAssets} Documents</span>
          </Badge>
          {otherAssets > 0 && (
            <Badge variant="secondary" className="flex items-center space-x-1">
              <FolderOpen className="h-3 w-3" />
              <span>{otherAssets} Other</span>
            </Badge>
          )}
        </div>
        
        {/* View Controls and Actions */}
        <div className="flex items-center space-x-2">
          {/* View Toggle */}
          <div className="flex items-center border rounded-md">
            <Button 
              variant={currentView === "grid" ? "default" : "ghost"} 
              size="sm"
              onClick={() => handleViewChange("grid")}
            >
              <Grid3X3 className="h-4 w-4" />
            </Button>
            <Button 
              variant={currentView === "list" ? "default" : "ghost"} 
              size="sm"
              onClick={() => handleViewChange("list")}
            >
              <List className="h-4 w-4" />
            </Button>
          </div>
          
          {/* Quick Filters */}
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="outline" size="sm">
                <Filter className="h-4 w-4 mr-2" />
                Quick Filters
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuLabel>Filter by Type</DropdownMenuLabel>
              <DropdownMenuItem>All Assets</DropdownMenuItem>
              <DropdownMenuItem>Images</DropdownMenuItem>
              <DropdownMenuItem>Videos</DropdownMenuItem>
              <DropdownMenuItem>Documents</DropdownMenuItem>
              <DropdownMenuItem>PDFs</DropdownMenuItem>
              <DropdownMenuItem>Other Files</DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuLabel>Filter by Project</DropdownMenuLabel>
              <DropdownMenuItem>All Projects</DropdownMenuItem>
              <DropdownMenuItem>Marketing Website</DropdownMenuItem>
              <DropdownMenuItem>Mobile App Design</DropdownMenuItem>
              <DropdownMenuItem>Brand Guidelines</DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuLabel>Filter by Assignment</DropdownMenuLabel>
              <DropdownMenuItem>My Assets</DropdownMenuItem>
              <DropdownMenuItem>Unassigned</DropdownMenuItem>
              <DropdownMenuItem>Recently Added</DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>

          {/* More Actions */}
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="outline" size="sm">
                <MoreHorizontal className="h-4 w-4" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuLabel>Bulk Actions</DropdownMenuLabel>
              <DropdownMenuItem>Download Selected</DropdownMenuItem>
              <DropdownMenuItem>Move to Project</DropdownMenuItem>
              <DropdownMenuItem>Change Assignment</DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuItem>Create Collection</DropdownMenuItem>
              <DropdownMenuItem>Generate Thumbnails</DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuItem>Archive Selected</DropdownMenuItem>
              <DropdownMenuItem className="text-red-600">Delete Selected</DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>

      {/* Storage and View Info */}
      <div className="flex items-center justify-between text-sm text-muted-foreground">
        <div className="flex items-center space-x-4">
          <span>Storage used: {formatFileSize(totalSize)}</span>
          <span>â€¢</span>
          <Badge variant="outline" className="capitalize">
            {currentView === "grid" && <Grid3X3 className="h-3 w-3 mr-1" />}
            {currentView === "list" && <List className="h-3 w-3 mr-1" />}
            {currentView} View
          </Badge>
        </div>
        <div className="flex items-center space-x-2">
          <span>Last updated: 2 hours ago</span>
        </div>
      </div>
    </div>
  );
}